import Vue from 'vue'
import VueRouter from 'vue-router'
import HomePage from '../views/HomePage.vue'
import LoginPage from '../views/LoginPage.vue'
import ForgetPasswordPage from '../views/ForgetPasswordPage.vue'
import ResetPasswordPage from '../views/ResetPasswordPage.vue'
import RegisterPage from '../views/RegisterPage.vue'
import DetailPage from '../views/DetailPage.vue'
import LeaderBoardPage from '../views/LeaderBoardPage.vue'
import ForumPage from '../views/ForumPage.vue'
import PostRecipePage from '../views/PostRecipePage.vue'
import NotFoundPage from '@/views/NotFoundPage.vue'
import Swal from 'sweetalert2'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home-Page',
    component: HomePage
  },
  {
    path: '/login',
    name: 'Login-Page',
    component: LoginPage
  },
  {
    path: '/forgetpassword',
    name: 'Forget-Password-Page',
    component: ForgetPasswordPage
  },
  {
    path: '/resetpassword/:token',
    name: 'Reset-Password-Page',
    component: ResetPasswordPage
  },
  {
    path: '/register',
    name: 'Register-Page',
    component: RegisterPage
  },
  {
    path: '/detail/:id',
    name: 'Detail-Page',
    component: DetailPage
  },
  {
    path: '/leaderboard',
    name: 'Leader-Board-Page',
    component: LeaderBoardPage
  },
  {
    path: '/forum',
    name: 'Forum-Page',
    component: ForumPage
  },
  {
    path: '/postrecipe',
    name: 'Post-Recipe-Page',
    component: PostRecipePage
  },
  {
    path: '*',
    name: 'NotFound-Page',
    component: NotFoundPage
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  if(to.name === 'Register-Page' || to.name === 'Home-Page' || to.name === 'Forget-Password-Page' ||  to.name === 'Reset-Password-Page' ||to.name === 'NotFound-Page') {
    next()
  } else {
    if (to.name !== 'Login-Page' && !localStorage.access_token) {
      next({ name: 'Login-Page' })
      Swal.fire({
        icon: 'warning',
        title: 'Haven\'t Logged in',
        text: 'Please Login First to access that content!!!'
      })
    }
    else {
      next()
    }
  }
})

export default router
